*,::after,::before{box-sizing:border-box;margin:0;padding:0}body{font-family:Monospaced Number,Chinese Quote,-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,PingFang SC,Hiragino Sans GB,Microsoft YaHei,Helvetica Neue,Helvetica,Arial,sans-serif}iframe,img,input,select,textarea{border:0;outline:0}input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}button:focus,input:focus,select:focus,textarea:focus{outline:0}a,a:active,a:hover,a:link,a:visited{text-decoration:none;color:inherit}::-ms-clear{display:none}::-webkit-search-cancel-button,::-webkit-search-decoration,::-webkit-search-results-button,::-webkit-search-results-decoration{display:none}li,ul{list-style:none}